<template>
    <section class="m-list" ref="car-list">
        <div class="banner-img"><slot></slot></div>
        <ul class="car-list" v-if="carList.length != 0">
            <li class="car-item" v-for="(item,index) in carList" :key="index">
                <div class="car-img">
                    <img :src="item.icon" alt="">
                </div>
                <div class="car-desc">
                    <div class="car-name">{{`${item.name}`}}</div>
                    <div class="car-price" v-if="carType == 2">
                        <span>{{`￥${item.nowPrice}`}}</span>
                        <span>{{`原价${item.primePrice}`}}</span>
                        <div class="car-labels" v-if = "item.labels">
                            <span class="label" v-for="(item,index) in getlabel(item.labels)" :key="index">
                                {{item}}
                            </span>
                        </div>
                    </div>
                    <div class="car-price" v-if="carType == 1">
                        <span><del>{{`首付${item.firstPayment}`}}</del></span>
                        <span>{{`月供${item.monthPayment}`}}</span>
                        <span class="period" v-if="item.period">{{`X${item.period}期`}}</span>
                    </div>
                    <div class="car-price" v-if="carType == 3">
                        <p class="car-msg">{{`${item.city}|${item.date}|${item.kilometers}万公里`}}</p>
                        <span><del>{{`首付${item.firstPayment}`}}</del></span>
                        <span>{{`月供${item.monthPayment}`}}</span>
                    </div>                          
                </div>
            </li>
            <li class="car-item">
                <a href="" class="more">更多车型<i class="icon-arrow"></i></a>
            </li>
        </ul>
    </section>
</template>

<script>
export default {
    props:{
        carList:Array,
        carType:Number
    },
    data:function(){
        return{
            
        }
    },
    mounted:function(){

    },
    methods:{
        getlabel:function(labels){
            return labels.split(",");
        }
    }
}
</script>



