<template>   
    <section class="m-home">
         <v-header>惠搜车</v-header>
         <scroll class="home-content" ref="homeContent">   
            <v-slider v-if="sliderArr.length">
                <div class="slider-item" v-for="(item,index) in sliderArr" :key="index">
                    <a :href="item.realUrl">
                        <img :src="item.imgUrl" alt="">
                    </a>
                </div>
            </v-slider>
            <v-nav></v-nav>
            <v-panel :title="brand" v-if="brandArr.length">
                <a  class="brand-item" v-for="(item,index) in brandArr" :key="index" @click="carRouter(item.id)">
                    <div class="img">
                        <img :src="item.brandIcon" alt="">
                    </div>                
                    <p>{{item.brand}}</p>
                </a>
                <a @click="brandList" class="brand-item all">
                    <div class="img">
                        <i class="icon-more "></i>
                    </div>
                    <p>全部品牌</p>
                </a>
            </v-panel>
            <v-list :carList ="leaseCarList" :carType= 1>
                <div class="car-banner" v-if="Object.keys(leaseCarbanner).length !=0">
                    <a :href="leaseCarbanner.realUrl"><img :src="leaseCarbanner.imgUrl" alt=""></a>    
                </div>                    
            </v-list>
            <v-list :carList ="importCarList" :carType= 2>
                <div class="car-banner" v-if="Object.keys(importCarbanner).length !=0">
                    <a :href="importCarbanner.realUrl"><img :src="importCarbanner.imgUrl" alt=""></a>    
                </div>                    
            </v-list>
            <v-list :carList ="secondCarList" :carType= 3>
                <div class="car-banner" v-if="Object.keys(secondCarbanner).length !=0">
                    <a :href="secondCarbanner.realUrl"><img :src="secondCarbanner.imgUrl" alt=""></a>    
                </div>                    
            </v-list>
            <v-panel :title="story" v-if="storyList.length">
                <ul class="story-list">
                    <li  class="list-item" v-for="(item,index) in storyList" :key="index">
                        <div class="img">
                            <img :src="item.icon" alt="">
                        </div>                
                        <div class="list-item-desc">
                            <h3>{{item.title}}</h3>
                            <p>{{item.date}}</p>
                        </div>
                    </li>
                </ul>             
            </v-panel>
            <v-panel :title="information" v-if="informationList.length">
                <ul class="information-list">
                    <li  class="list-item" v-for="(item,index) in informationList" :key="index">
                        <div class="img">
                            <img :src="item.icon" alt="">
                        </div>                
                        <div class="list-item-desc">
                            <h3>{{item.title}}</h3>
                            <p>{{item.date}}<span class="type">{{item.type}}</span></p>
                        </div>
                    </li>
                </ul>             
            </v-panel>
            <div class="loading-container" v-if="sliderArr.length == 0">
                <v-loading></v-loading>
            </div>            
         </scroll>       
         <router-view></router-view>  
    </section>   
</template>

<script>
import vHeader from '@/components/mobile/m-header'
import vSlider from '@/components/mobile/m-slider'
import vNav from '@/components/mobile/m-nav'
import Scroll from '@/components/base/scroll/scroll'
import vPanel from '@/components/mobile/m-panel'
import vList from '@/components/mobile/m-list'
import vLoading from '@/components/base/loading/loading'
import {MOBILE_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return{
            sliderArr:[],
            brand:"品牌",
            story:"车主故事",
            information:"资讯",
            brandArr:[],
            leaseCarList:[],
            importCarList:[],
            secondCarList:[],
            leaseCarbanner:{},
            secondCarbanner:{},
            importCarbanner:{},
            storyList:[],
            informationList:[]
        }
    },
    components:{
        vHeader,
        vSlider,
        vNav,
        vPanel,
        vList,
        vLoading,
        Scroll
    },
    methods:{
        getHomeData:function(){
            let _this = this;
            $.ajax({
                url:MOBILE_COMMONURL+"/index",
                type:"get",
                data:{
                    'platForm':'020005'
                },
                dataType:'json',
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        _this.sliderArr = data.data.carousel;
                        _this.brandArr = data.data.brandList;
                        _this.leaseCarbanner = data.data.leaseCarImage;
                        _this.leaseCarList = data.data.leaseList;
                        _this.importCarbanner = data.data.importCarImage;
                        _this.importCarList = data.data.importList;
                        _this.secondCarbanner = data.data.secondHandCarImage;
                        _this.secondCarList = data.data.secondList;
                        _this.storyList = data.data.storyList;
                        _this.informationList = data.data.newList;
                    }
                }
            })
        },
        carRouter:function(id){
            this.$router.push({
                name: 'mobileSearchCar',
                params: {
                    id: id
                }
            })
        },
        brandList:function(){
            this.$router.push({
                path:'/home/brandList'
            })
        }
    },
    mounted:function(){
        this.getHomeData();
        this.$refs.homeContent.$el.style.top = `44px`; 
    }
}
</script>


